// Series navigation styling
.series-nav {
 margin: 2em 0;
 padding: 1.2em;
 width: 100%;
 background-color: var(--global-bg-color);
 border: 2px solid var(--global-divider-color);
 border-radius: 4px;

 .series-header {
 margin-bottom: 0.75em;
 border: none !important;
 border-bottom: none !important;
 padding: 0;

 .title-row {
 display: flex;
 align-items: center;
 gap: 0.75em;
 margin-bottom: 0.3em;
 }

 h3 {
 margin: 0;
 padding: 0;
 border: none !important;
 border-bottom: none !important;
 font-size: 0.8rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.08em;
 color: var(--global-theme-color);
 &::after {
 display: none !important;
 }
 }

 p {
 margin: 0;
 padding: 0;
 color: var(--global-text-color-light);
 font-size: 0.85em;
 line-height: 1.4;
 }
 }

 .part-badge {
 background-color: var(--global-theme-color);
 color: white;
 padding: 0.2em 0.5em;
 border-radius: 3px;
 font-size: 0.75rem;
 font-weight: 500;
 white-space: nowrap;
 }

 .posts {
 display: flex;
 flex-direction: column;
 gap: 0;

 a {
 text-decoration: none !important;
 border-bottom: none !important;

 &:hover {
 text-decoration: none !important;
 border-bottom: none !important;
 }
 }
 }

 .post {
 display: flex;
 align-items: flex-start;
 padding: 0.5em 0.6em;
 border-bottom: 1px solid var(--global-divider-color);
 border-radius: 3px;

 &:last-child {
 border-bottom: none;
 }

 &.inactive {
 cursor: pointer;

 .post-title {
 color: var(--global-text-color-light);
 }

 .post-description {
 color: var(--global-text-color-light);
 }

 &:hover {
 background-color: color-mix(in srgb, var(--global-theme-color) 8%, transparent);
 border-color: var(--global-theme-color);

 .post-title,
 .post-description {
 color: var(--global-theme-color);
 }
 }
 }
 }

 .post-content {
 flex: 1;
 min-width: 0;
 }

 .post-title {
 font-weight: 600;
 text-decoration: none !important;
 color: var(--global-text-color);
 font-size: 0.95em;
 line-height: 1.3;
 }

 .post-description {
 display: -webkit-box;
 -webkit-line-clamp: 2;
 -webkit-box-orient: vertical;
 overflow: hidden;
 margin-top: 0.1em;
 color: var(--global-text-color-light);
 font-style: italic;
 font-size: 0.8em;
 line-height: 1.3;
 }
}
