/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography

p, h1, h2, h3, h4, h5, h6, em, div, li, span, strong {
 color: var(--global-text-color);
}

hr {
 border-top: 1px solid var(--global-divider-color);
}

table {
 td, th {
 color: var(--global-text-color);
 }
 td {
 font-size: 1rem;
 }
}

a, table.table a {
 color: var(--global-theme-color);
 &:hover {
 color: var(--global-theme-color);
 text-decoration: underline;
 }
 &:hover:after :not(.nav-item.dropdown) {
 width: 100%;
 }
}

figure, img {
 max-width: 90vw;
}

blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1.5em 10px;
 padding: 0.5em 10px;
 font-size: 1.2rem;
}

// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 font-size: 0.875rem;
 margin-top: 0.75rem;
 margin-bottom: 1.5rem;
 text-align: center;
 color: var(--global-text-color);
}

// Card

.card {
 background-color: var(--global-card-bg-color);

 img {
 width: 100%;
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }
}

// Citation

.citation, .citation-number {
 color: var(--global-theme-color);
}

// Profile

.profile {
 width: 100%;

 .address {
 margin-bottom: 5px;
 margin-top: 5px;
 font-family: monospace;
 p {
 display: inline-block;
 margin: 0;
 }
 }
}
.profile.float-right{
 margin-left: 1rem;
}
.profile.float-left{
 margin-right: 1rem;
}

@media (min-width: 576px) {
 .profile {
 width: 30%;
 .address {
 p { display: block; }
 }
 }
}

.post-description {
 margin-bottom: 2rem;
 font-size: 0.875rem;
 a {
 color: inherit;
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

// Blog post tags
.tag {
 background-color: transparent;
 color: var(--global-text-color-light) !important;
 padding: 0.15rem 0.5rem;
 border: 1px solid var(--global-divider-color);
 border-radius: 3px;
 font-size: 0.7rem;
 text-decoration: none;
 display: inline-block;
 text-transform: uppercase;
 letter-spacing: 0.03em;
 transition: all 0.2s ease;

 &:hover {
 background-color: transparent;
 color: var(--global-theme-color) !important;
 border-color: var(--global-theme-color);
 text-decoration: none;
 }
}

// Navbar customization

.navbar {
 box-shadow: none;
 border-bottom: 1px solid var(--global-divider-color);
 background-color: var(--global-bg-color);
 opacity: 0.95;
}
.navbar .dropdown-menu {
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);
 a:not(.active) {
 color: var(--global-text-color);
 }
 a:hover {
 color: var(--global-hover-color);
 }
 .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
 }
}
.dropdown-item {
 color: var(--global-text-color);
 &:hover {
 color: var(--global-hover-color);
 background-color: var(--global-bg-color);
 }
}
.navbar.navbar-light {
 a {
 &:hover {
 text-decoration: none;
 }
 }
 .navbar-brand {
 color: var(--global-text-color);
 }
 .navbar-nav .nav-item .nav-link {
 color: var(--global-text-color);
 &:hover {
 color: var(--global-hover-color);
 }
 }
 .navbar-nav .nav-item.active>.nav-link {
 background-color: inherit;
 font-weight: bolder;
 color: var(--global-theme-color);
 &:hover {
 color: var(--global-hover-color);
 }
 }
 .navbar-brand.social {
 padding-bottom: 0;
 padding-top: 0;
 font-size: 1.7rem;
 a {
 i::before {
 color: var(--global-text-color);
 transition-property: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }
}

.navbar-toggler {
 .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-text-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
 }
 .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
 }
 .middle-bar {
 opacity: 0;
 }
 .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
 }
}

.navbar-toggler.collapsed {
 .top-bar {
 transform: rotate(0);
 }
 .middle-bar {
 opacity: 1;
 }
 .bottom-bar {
 transform: rotate(0);
 }
}

#light-toggle {
 padding: 0;
 border: 0;
 background-color: inherit;
 color: var(--global-text-color);
 &:hover {
 color: var(--global-hover-color);
 }
}

// Social (bottom)

.social {
 text-align: center;
 .contact-icons {
 font-size: 3rem; /* This was changed from 4 to 3 by myself. */
 a {
 i::before {
 color: var(--global-text-color);
 transition-property: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }
 .contact-note {
 font-size: 0.8rem;
 }
}


// Footer
footer.fixed-bottom {
 background-color: var(--global-footer-bg-color);
 font-size: 0.75rem;
 .container {
 color: var(--global-footer-text-color);
 padding-top: 9px;
 padding-bottom: 8px;
 }
 a {
 color: var(--global-footer-link-color);
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

footer.sticky-bottom {
 border-top: 1px solid var(--global-divider-color);
 padding-top: 40px;
 padding-bottom: 40px;
 font-size: 0.9rem;
}

// Section header (reusable minimal style matching essays aesthetic)

.section-header {
 font-size: 0.8rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.08em;
 color: var(--global-theme-color);
 padding-bottom: 0.3rem;
 border-bottom: 2px solid var(--global-theme-color);
 margin-bottom: 1rem;
}

// News list

.news-list {
 .news-entry {
 display: flex;
 gap: 1rem;
 padding: 0.3rem 0;
 border-bottom: 1px solid var(--global-divider-color);

 &:last-child {
 border-bottom: none;
 }
 }

 .news-date {
 flex: 0 0 auto;
 min-width: 55px;
 font-size: 0.85rem;
 color: var(--global-text-color-light);
 white-space: nowrap;
 }

 .news-content {
 flex: 1;
 font-size: 0.95rem;
 }
}

// About page callout boxes

.about-callout {
 border-left: 3px solid var(--global-theme-color);
 background: var(--global-code-bg-color);
 padding: 0.65rem 0.9rem;
 border-radius: 0 4px 4px 0;
 margin-bottom: 0.6rem;

 &.about-callout-role {
 line-height: 1.7;
 }

 &.about-callout-hiring {
 font-size: 0.9rem;
 }
}

// CV

.cv {
 margin-bottom: 40px;

 .list-group {
 border: none;
 }

 .list-group-item {
 background-color: transparent !important;
 border-color: var(--global-divider-color) !important;
 border-left: none !important;
 border-right: none !important;
 border-top: none !important;
 border-bottom: 1px solid var(--global-divider-color) !important;
 padding-left: 0;
 padding-right: 0;
 }

 .cv-time-table {
 .cv-entry {
 padding: 0.6rem 0;
 border-bottom: 1px solid var(--global-divider-color);

 &:last-child {
 border-bottom: none;
 }
 }

 .cv-entry-inner {
 display: flex;
 gap: 1.5rem;
 }

 .cv-year {
 flex: 0 0 auto;
 min-width: 130px;
 font-size: 1rem;
 font-weight: 600;
 color: var(--global-text-color);
 padding-top: 0;
 white-space: nowrap;
 }

 .cv-details {
 flex: 1;
 min-width: 0;
 }

 .cv-title {
 font-weight: 600;
 margin-bottom: 0.1rem;
 }

 .cv-institution {
 font-size: 0.95rem;
 color: var(--global-text-color);
 margin-bottom: 0.25rem;
 }

 .cv-items {
 margin: 0.25rem 0 0 0;
 padding-left: 1.2rem;
 font-size: 0.9rem;

 li {
 margin-bottom: 0.1rem;
 }
 }

 .cv-subitems {
 padding-left: 1rem;
 font-size: 0.85rem;
 }
 }
}

// Repositories

@media (min-width: 768px) {
 .repo {
 max-width: 50%;
 }
}

// Blog

.header-bar {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 1rem;
 padding-bottom: 1.25rem;
 margin-bottom: 1.5rem;
 h1 {
 color: var(--global-theme-color);
 font-size: 2rem;
 font-weight: 400;
 }
 h2 {
 font-size: 0.95rem;
 color: var(--global-text-color-light);
 margin-top: 0.25rem;
 font-weight: 400;
 }
}

.tag-list {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 1rem;

 ul {
 justify-content: center;
 display: flow-root;

 p, li {
 list-style: none;
 display: inline-block;
 padding: 1rem 0.5rem;
 color: var(--global-text-color-light);
 }
 }
}

.post-list {
 margin: 0;
 margin-bottom: 40px;
 padding: 0;
 li {
 border-bottom: 1px solid var(--global-divider-color);
 list-style: none;
 padding-top: 2rem;
 padding-bottom: 2rem;
 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }
 a {
 color: var(--global-text-color);
 text-decoration: none;
 &:hover {
 color: var(--global-theme-color);
 }
 }
 }

 .post-preview {
 padding: 0.6rem 0;
 border-bottom: 1px solid var(--global-divider-color);

 &:last-child {
 border-bottom: none;
 }

 .post-title, .post-title-link {
 font-size: 1.15rem;
 font-weight: 400;
 margin-bottom: 0.1rem;

 a {
 color: var(--global-text-color);
 text-decoration: none;
 &:hover {
 color: var(--global-theme-color);
 }
 }
 }

 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.8rem;
 margin-bottom: 0.2rem;
 }

 .post-description {
 font-size: 0.9rem;
 margin-bottom: 0;
 color: var(--global-text-color);
 }
 }
}

.pagination {
 .page-item {
 .page-link {
 color: var(--global-text-color);
 &:hover {
 color: $black-color;
 }
 }
 &.active .page-link {
 color: $white-color;
 background-color: var(--global-theme-color);
 &:hover {
 background-color: var(--global-theme-color);
 }
 }
 }
}


// Distill

.distill {
 a:hover {
 border-bottom-color: var(--global-theme-color);
 text-decoration: none;
 }
}


// Projects

.projects {
 a {
 text-decoration: none;

 &:hover {
 .card-title {
 color: var(--global-theme-color);
 }
 }
 }

 .card {
 img {
 width: 100%;
 }
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }

 .grid-sizer, .grid-item {
 width: 250px;
 margin-bottom: 10px;
 }

 h2.category {
 color: var(--global-divider-color);
 border-bottom: 1px solid var(--global-divider-color);
 padding-top: 0.5rem;
 margin-top: 2rem;
 margin-bottom: 1rem;
 text-align: right;
 }
}


// Publications

.publications {
 margin-top: 2rem;
 h1 {
 color: var(--global-theme-color);
 font-size: 2rem;
 text-align: center;
 margin-top: 1em;
 margin-bottom: 1em;
 }
 h2 {
 margin-bottom: 1rem;
 span {
 font-size: 1.5rem;
 }
 }
 h2.year {
 color: var(--global-divider-color);
 border-top: 1px solid var(--global-divider-color);
 padding-top: 1rem;
 margin-top: 2rem;
 margin-bottom: -2rem;
 text-align: right;
 }
 ol.bibliography {
 list-style: none;
 padding: 0;
 margin-top: 0;

 li {
 // Use :has() to collapse li when its .entry child is hidden by JS filter
 &:has(.entry[style*="display: none"]) {
 display: none;
 }

 .row.entry {
 padding-bottom: 1rem;
 margin-bottom: 1rem;
 margin-left: 0;
 margin-right: 0;
 border-bottom: 1px solid var(--global-divider-color);
 }

 &:last-child .row.entry {
 border-bottom: none;
 }

 .pub-thumb-col {
 flex: 0 0 75px;
 max-width: 75px;
 padding-right: 15px;

 @media (max-width: 575px) {
 display: none;
 }
 }

 .pub-content-col {
 flex: 1;
 min-width: 0;
 }

 .preview-container {
 position: relative;
 display: flex;
 flex-direction: column;
 align-items: flex-start;
 }

 .preview {
 width: 60px;
 min-width: 60px;
 max-width: 60px;
 margin-bottom: 0.25rem;
 overflow: visible;

 img {
 width: 100%;
 height: auto;
 border-radius: 4px;
 box-shadow: none !important;
 cursor: zoom-in;
 transition: transform 0.25s ease;

 &:hover {
 transform: scale(2.5);
 transform-origin: top left;
 z-index: 10;
 position: relative;
 border-radius: 4px;
 box-shadow: 0 4px 15px rgba(0,0,0,0.15) !important;
 }
 }
 }
 
 .abbr-tag {
 margin-top: 0.25rem;
 
 .badge {
 display: inline-block;
 background-color: var(--global-theme-color);
 padding: 0.15rem 0.5rem;
 border-radius: 3px;
 font-size: 0.75rem;
 
 a {
 color: white;
 &:hover {
 text-decoration: none;
 }
 }
 }
 }
 
 .abbr {
 height: 2rem;
 margin-bottom: 0.5rem;
 
 .badge {
 display: inline-block;
 background-color: var(--global-theme-color);
 padding: 0.5rem 1rem;
 border-radius: 3px;
 
 a {
 color: white;
 &:hover {
 text-decoration: none;
 }
 }
 }
 }
 
 .award {
 color: var(--global-theme-color) !important;
 border: 1px solid var(--global-theme-color);
 }
 .title {
 font-weight: bolder;
 }
 .author {
 a {
 color: inherit;
 text-decoration: none;
 border-bottom: 1px dotted rgba(128, 128, 128, 0.4);
 &:hover {
 border-bottom-color: var(--global-theme-color);
 }
 }
 > em {
 border-bottom: 1px solid;
 font-style: normal;
 }
 > span.more-authors {
 color: inherit;
 border-bottom: 1px dotted rgba(128, 128, 128, 0.4);
 cursor: pointer;
 &:hover {
 color: inherit;
 border-bottom-color: var(--global-theme-color);
 }
 }
 }
 .links {
 display: flex;
 flex-wrap: wrap;
 align-items: center;
 gap: 0.25rem;
 margin-top: 0.35rem;

 a.btn {
 margin: 0 !important;
 padding: 0.15rem 0.5rem !important;
 border: 1px solid var(--global-divider-color) !important;
 border-radius: 3px !important;
 color: var(--global-text-color-light) !important;
 background: transparent !important;
 background-color: transparent !important;
 font-size: 0.75rem;
 text-transform: lowercase;
 line-height: 1.4;
 letter-spacing: 0.02em;
 box-shadow: none !important;
 transition: all 0.2s ease;

 &:hover, &:focus {
 color: var(--global-theme-color) !important;
 background: transparent !important;
 background-color: transparent !important;
 border-color: var(--global-theme-color) !important;
 text-decoration: none;
 box-shadow: none !important;
 }
 }
 }
 .badges {
 span {
 display: inline-block;
 color: $black-color;
 height: 100%;
 padding-left: 0.5rem;
 vertical-align: middle;
 &:hover {
 text-decoration: underline;
 }
 }
 }
 .hidden {
 font-size: 0.875rem;
 max-height: 0px;
 overflow: hidden;
 text-align: justify;
 transition: max-height 0.15s ease;

 p {
 line-height: 1.4em;
 margin: 10px;
 }
 pre {
 font-size: 1em;
 line-height: 1.4em;
 padding: 10px;
 }
 }
 .hidden.open {
 max-height: 100em;
 transition: max-height 0.15s ease;
 }
 div.abstract.hidden {
 border: none;
 }
 div.abstract.hidden.open {
 border: none;
 }
 .periodical {
 margin-bottom: 0.25rem;
 display: flex;
 align-items: center;
 flex-wrap: wrap;

 @media (max-width: 575px) {
 flex-wrap: nowrap;
 }

 .badge {
 display: inline-flex;
 align-items: center;
 background-color: var(--global-theme-color);
 padding: 0.15rem 0.5rem;
 border-radius: 3px;
 font-size: 0.75rem;
 line-height: 1;
 white-space: nowrap;
 transform: translateY(-0.5px);
 flex-shrink: 0;

 a {
 color: white;
 &:hover {
 text-decoration: none;
 }
 }
 }

 .venue-name {
 margin-left: 0.5rem;
 flex: 1;
 min-width: 0;

 @media (max-width: 575px) {
 white-space: nowrap;
 overflow: hidden;
 text-overflow: ellipsis;
 }
 }

 .year {
 white-space: nowrap;
 margin-left: auto;
 padding-left: 1rem;
 color: var(--global-theme-color);
 opacity: 0.8;
 flex-shrink: 0;
 }
 }
 }
 }
}

// Rouge Color Customization
figure.highlight {
 margin: 0 0 1rem;
}

pre {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 6px;
 padding: 6px 12px;
 pre, code {
 background-color: transparent;
 border-radius: 0;
 padding: 0;
 }
}

code {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 3px;
 padding: 3px 3px;
}


// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
 transition: color 750ms, background-color 750ms, border-color 750ms, fill 750ms, stroke 750ms, box-shadow 750ms, outline-color 750ms !important;
 transition-delay: 0 !important;
}

