.post{
 .post-meta{
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }
 .post-content{
 blockquote {
 border-left: 5px solid var(--global-theme-color);
 padding: 8px;
 }

 h2 {
 font-size: 0.85rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.08em;
 color: var(--global-theme-color);
 padding-bottom: 0.3rem;
 border-bottom: 2px solid var(--global-theme-color);
 margin-top: 2rem;
 margin-bottom: 1rem;
 }

 h3 {
 font-size: 0.8rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.06em;
 color: var(--global-theme-color);
 margin-top: 1.5rem;
 margin-bottom: 0.75rem;
 }
 }

 .page-content {
 h2 {
 font-size: 0.85rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.08em;
 color: var(--global-theme-color);
 padding-bottom: 0.3rem;
 border-bottom: 2px solid var(--global-theme-color);
 margin-top: 2rem;
 margin-bottom: 1rem;
 }

 h3 {
 font-size: 0.8rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.06em;
 color: var(--global-theme-color);
 margin-top: 1.5rem;
 margin-bottom: 0.75rem;
 }

 h4 {
 font-size: 0.8rem;
 font-weight: 600;
 color: var(--global-text-color);
 margin-top: 1.25rem;
 margin-bottom: 0.5rem;
 }
 }

 .post-header {
 border-bottom: 1px solid var(--global-divider-color);
 padding-bottom: 1rem;
 margin-bottom: 1.5rem;

 .post-title {
 color: var(--global-theme-color);
 font-size: 1.5rem;
 font-weight: 400;
 margin-bottom: 0.3rem;
 }

 .post-meta {
 font-size: 0.85rem;
 color: var(--global-text-color-light);
 margin-bottom: 0;

 .tag {
 vertical-align: baseline;
 }
 }
 }
}

progress {
 /* Positioning */
 position: fixed;
 left: 0;
 top: 56px;
 z-index: 10;

 /* Dimensions */
 width: 100%;
 height: 1px;

 /* Reset the appearance */
 -webkit-appearance: none;
 -moz-appearance: none;
 appearance: none;

 /* Get rid of the default border in Firefox/Opera. */
 border: none;

 /* Progress bar container for Firefox/IE10 */
 background-color: transparent;

 /* Progress bar value for IE10 */
 color: var(--global-theme-color);
 }

 progress::-webkit-progress-bar {
 background-color: transparent;
 }

 progress::-webkit-progress-value {
 background-color: var(--global-theme-color);
 }

 progress::-moz-progress-bar {
 background-color: var(--global-theme-color);
 }

 .progress-container {
 width: 100%;
 background-color: transparent;
 position: fixed;
 top: 56px;
 left: 0;
 height: 5px;
 display: block;
 }

 .progress-bar {
 background-color: var(--global-theme-color);
 width: 0%;
 display: block;
 height: inherit;
 }

.publication-tags {
 margin-bottom: 1.5rem;
 display: flex;
 flex-wrap: wrap;
 gap: 0.3rem;
}
