.series-list-container {
 margin-bottom: 2rem;
}

.series-list {
 display: flex;
 flex-wrap: wrap;
 gap: 0.4rem;
}

.series-box {
 display: inline-block;
 padding: 0.3rem 0.7rem;
 border: 1px solid var(--global-divider-color);
 border-radius: 3px;
 text-decoration: none;
 color: var(--global-text-color-light);
 background-color: transparent;
 transition: all 0.2s ease;
 font-size: 0.85rem;
 line-height: 1.5;
}

.series-box:hover {
 color: var(--global-theme-color);
 background-color: transparent;
 border-color: var(--global-theme-color);
 transform: none;
 box-shadow: none;
 text-decoration: none;
}

.series-title {
 font-size: inherit;
 font-weight: normal;
 margin-bottom: 0;
 color: inherit;
}

.series-start-link {
 font-size: 0.95em;
 opacity: 0.9;
}
